import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  SET_MESSAGE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGOUT
} from "../../constants/Type";
import UserAuthenticationService from "../../services/authentication/UserAuthenticationService";

export const LoginAction = (username, password) => (dispatch) => {
  dispatch({
    type: LOGIN_REQUEST,
  });

  return UserAuthenticationService.Login(username, password).then(
    (response) => {
      dispatch({
        type: LOGIN_SUCCESS,
        payload: { user: response.data },
      });
      return Promise.resolve();
    },
    (error) => {
      dispatch({
        type: LOGIN_FAIL,
        error: error
      });

      dispatch({
        type: SET_MESSAGE,
        payload: error.message,
      });
      return Promise.reject();
    }
  );
};

export const RegisterAction = (UserObj) => (dispatch) => {
  dispatch({
    type: REGISTER_REQUEST,
  });

  return UserAuthenticationService.Register(UserObj).then(
    (response) => {
      dispatch({
        type: REGISTER_SUCCESS,
      });

      dispatch({
        type: SET_MESSAGE,
        payload: response.message,
      });

      return Promise.resolve();
    },
    (error) => {
      dispatch({
        type: REGISTER_FAIL,
      });

      dispatch({
        type: SET_MESSAGE,
        payload: error,
      });

      return Promise.reject();
    }
  );
};

export const logout = () => (dispatch) => {
    UserAuthenticationService.logout();
    dispatch({
      type: LOGOUT,
    });
  };
